.logo {
	@extend %d-flex;
	@extend %align-items-center;
	@extend %justify-content-center;

	width: 70px;

	@include media-breakpoint-up(md) {
		width: 164px;
	}

	&__img {
		@include transition('width, height');

		@extend %d-block;

		height: 46px;

		@include media-breakpoint-up(md) {
			height: 90px;
		}

		@include media-breakpoint-up(xl) {
			height: 108px;
		}

		&.is-small {
			@include media-breakpoint-up(md) {
				height: 75px;
			}
		}
	}
}
