.btn {
	--width: 20px;
	--time: 700ms;

	position: relative;
	width: 227px;
	height: 53px;
	overflow: hidden;
	color: hsl(0, 0%, 100%);
	cursor: pointer;
	padding: {
		right: 30px;
		bottom: 4px;
		left: 10px;
	}
	border: {
		radius: 6px;
	}
	background: {
		color: hsl(266, 44%, 74%);
	}
	font: {
		weight: 500;
		size: 16px;
	}
	text: {
		align: center;
	}

	* {
		pointer-events: none;
	}

	clip-path: url(#btn-mobile);

	@include media-breakpoint-up(md) {
		width: 286px;
		height: 67px;
		font: {
			size: 20px;
		}

		clip-path: url(#btn);
	}

	&__hover {
		position: absolute;
		z-index: 999;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}

	&__animated {
		@extend %d-block;

		position: absolute;
		z-index: 2;
		width: 0;
		height: 0;
		transform: translate(-50%, -50%);
		transition: width var(--time), padding-top var(--time);
		border-radius: 100%;
		background: hsl(0, 0%, 100%);

		&::before {
			content: '';
			position: absolute;
			top: -30%;
			right: -30%;
			bottom: -30%;
			left: -30%;
			width: 160%;
			height: 160%;
			border-radius: 100%;
			background: {
				image: radial-gradient(hsl(0, 0%, 100%), hsla(0, 0%, 100%, 0));;
			}
		}
	}

	&__text {
		position: relative;
		z-index: 999;
		transition: color 300ms;
	}

	&--small {
		width: 157px;
		height: 53px;
		clip-path: url(#btn-small-mobile);

		@include media-breakpoint-up(md) {
			width: 198px;
			height: 67px;
			clip-path: url(#btn-small);
		}
	}

	&:hover {
		.btn__text {
			color: hsl(266, 44%, 74%);
		}

		.btn__animated {
			width: calc(var(--width) * 30);
			padding-top: calc(var(--width) * 24);
		}
	}
}

a,
div {
	&.btn {
		@extend %d-inline-flex;
		@extend %align-items-center;
		@extend %justify-content-center;
	}
}
