.sale {
	overflow: hidden;

	&__header {
		margin: {
			bottom: 40px;
		}

		@include media-breakpoint-up(md) {
			margin: {
				bottom: 80px;
			}
		}

		.icon {
			color: hsl(0, 0%, 100%);
		}
	}

	&--action-page {
		z-index: 6;

		padding: {
			top: 8%;
			bottom: 14%;
		}

		.headline__line {
			.icon {
				color: hsl(269, 31%, 82%);
			}
		}
	}
}

.mobile-margin {
	@include media-breakpoint-only(xs) {
		margin: {
			top: -150px;
		}
	}
}
