#js-wave {
	@include transition('transform');

	&.is-scroll {
		transform: scale(1, 0);
	}
}

.header {
	position: fixed;
	z-index: 999;
	top: 0;
	right: 0;
	left: 0;
	background: {
		position: center;
		color: hsl(270, 30%, 82%);
		image: url(/images/standard/bg.png);
	}
	font: {
		size: 12px;
		weight: 500;
	}

	&__container {
		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-between;
		@extend %flex-nowrap;

		max-width: 1800px;
		padding: {
			top: 8px;
			bottom: 8px;
		}
	}
}

@import './header/hamburger';
@import './header/lang';
@import './header/logo';
@import './header/menu';
