
.menu {
	@include transition('opacity');

	position: absolute;
	top: 62px;
	left: 0;
	width: 100%;
	height: calc(100vh - 62px);
	overflow: auto;
	opacity: 0;
	pointer-events: none;

	background: {
		position: center bottom;
		color: hsl(270, 30%, 82%);
		image: url(/images/standard/bg.png);
	}

	@include media-breakpoint-up(md) {
		position: relative;
		top: auto;
		width: auto;
		height: auto;
		transition: none;
		background: {
			color: transparent;
			image: none;
		}

		opacity: 1;
		pointer-events: all;
	}

	&__list {
		@extend %d-md-flex;
		@extend %justify-content-md-center;

		padding: {
			right: 15px;
			left: 15px;
		}

		@include media-breakpoint-down(sm) {
			height: calc(100vh - 180px);
			margin-top: 30px;
			overflow: auto;
		}
	}

	&__list-line {
		width: 100%;
		height: 20px;
		color: hsla(267, 27%, 56%, .2);

		&.icon {
			@extend %d-md-none;
		}
	}

	&__list-caret {
		@include transition('left, transform');

		position: absolute;
		z-index: 1;
		top: calc(50% - 23px);
		left: 0;
		width: 46px;
		height: 46px;
		transform: scale(0);
		color: hsl(54, 23%, 91%);
		pointer-events: none;

		@include media-breakpoint-up(md) {
			top: calc(50% - 12px);
			width: 24px;
			height: 24px;
		}

		@include media-breakpoint-up(lg) {
			top: calc(50% - 15px);
			width: 30px;
			height: 30px;
		}

		@include media-breakpoint-up(xl) {
			top: calc(50% - 23px);
			width: 46px;
			height: 46px;
		}

		&.icon {
			@extend %d-none;
			@extend %d-md-flex;
		}
	}

	&.is-active {
		@include media-breakpoint-down(sm) {
			z-index: 999;
			height: calc(100vh - 62px);
			opacity: 1;
			pointer-events: all;
		}
	}

	&__item {
		@include transition('color');

		@extend %d-flex;
		@extend %align-items-center;

		position: relative;
		z-index: 2;
		height: 50px;
		font-size: 20px;
		white-space: nowrap;
		padding: {
			right: 0;
			left: 0;
		}

		@include media-breakpoint-down(sm) {
			line-height: 1;
			font: {
				family: 'Caveat', sans-serif;
				size: 30px;
			}
		}

		@include media-breakpoint-up(md) {
			padding: {
				right: 6px;
				left: 6px;
			}

			font-size: 14px;
		}

		@include media-breakpoint-up(lg) {
			padding: {
				right: 16px;
				left: 16px;
			}

			font-size: 16px;
		}

		@include media-breakpoint-up(xl) {
			padding: {
				right: 22px;
				left: 22px;
			}

			font-size: 20px;
		}

		&:hover {
			color: hsl(54, 23%, 91%);
		}
	}

	&__social {
		@extend %d-md-none;

		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		background: hsl(267, 27%, 56%);
		color: hsl(20, 16%, 96%);
		padding: {
			right: 15px;
			left: 15px;
		}

		.social {
			padding: {
				top: 10px;
				bottom: 30px;
			}
		}

		.social__list {
			margin: {
				top: 15px;
			}
		}

		.social__item {
			margin: {
				right: 30px;
			}

			font-weight: 400;
		}
	}
}
