.headline {
	font: {
		family: 'Caveat', sans-serif;
		size: 34px;
		weight: 700;
	}

	line-height: 1;

	&__line {
		@extend %d-inline-block;

		position: relative;
		line-height: 1;
		white-space: nowrap;

		span {
			position: relative;
			z-index: 2;
		}

		.icon {
			position: absolute;
			z-index: 1;
			bottom: -18%;
			left: 0;
			width: 100%;
			height: 46%;
			color: hsl(269, 31%, 82%);
		}
	}

	@include media-breakpoint-up(sm) {
		font: {
			size: 40px;
		}
	}

	@include media-breakpoint-up(md) {
		font: {
			size: 60px;
		}
	}

	@include media-breakpoint-up(xl) {
		font: {
			size: 90px;
		}
	}

	&--product {
		font: {
			size: 120px;
		}
	}
}
