.lang {
	@extend %d-flex;
	@extend %justify-content-end;

	margin-left: auto;

	font: {
		weight: 500;
		size: 14px;
	}

	@include media-breakpoint-up(md) {
		margin-left: 0;
	}

	@include media-breakpoint-up(lg) {
		font-size: 16px;
	}

	@include media-breakpoint-up(xl) {
		font-size: 20px;
	}

	&__item {
		@include transition('color');

		position: relative;
		margin: {
			left: 10px;
		}

		@include media-breakpoint-up(lg) {
			margin: {
				left: 18px;
			}
		}

		&:hover {
			color: hsl(54, 23%, 91%);
		}

		.icon {
			position: absolute;
			z-index: 1;
			top: calc(50% - 12px);
			left: calc(50% - 12px);
			width: 24px;
			height: 24px;
			opacity: 0;
			color: hsl(54, 23%, 91%);
			pointer-events: none;

			@include media-breakpoint-up(lg) {
				top: calc(50% - 15px);
				left: calc(50% - 15px);
				width: 30px;
				height: 30px;
			}

			@include media-breakpoint-up(xl) {
				top: calc(50% - 23px);
				left: calc(50% - 23px);
				width: 46px;
				height: 46px;
			}
		}

		span {
			position: relative;
			z-index: 2;
		}

		&.is-active {
			pointer-events: none;

			.icon {
				opacity: 1;
			}
		}
	}
}
