$hamburger-padding-x: 0 !default;
$hamburger-padding-y: 0 !default;
$hamburger-layer-width: 22px !default;
$hamburger-layer-height: 2px !default;
$hamburger-layer-spacing: 6px !default;
$hamburger-layer-color: $text-color !default;
$hamburger-layer-border-radius: 0 !default;
$hamburger-hover-opacity: .7 !default;
$hamburger-active-layer-color: $hamburger-layer-color !default;
$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;

$hamburger-hover-use-filter: false !default;
$hamburger-hover-filter: opacity(50%) !default;
$hamburger-active-hover-filter: $hamburger-hover-filter !default;

$hamburger-types: (slider) !default;

@import '~hamburgers/_sass/hamburgers/base';
@import '~hamburgers/_sass/hamburgers/types/slider';

.hamburger-wrapper {
	@extend %d-flex;
	@extend %d-md-none;

	width: 22px;
	height: 18px;
	margin: {
		left: 20px;
	}
}

.hamburger {
	@extend %d-flex;
	@extend %justify-content-between;
	@extend %align-items-center;

	height: 18px;
}
