
.tab {
	@extend %d-flex;
	@extend %align-items-center;
	@extend %justify-content-center;

	&__list {
		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-center;

		position: relative;
	}

	&__control {
		@include transition(left);

		position: absolute;
		z-index: 1;
		top: calc(50% - 25px);

		@include media-breakpoint-up(sm) {
			top: calc(50% - 35px);
		}

		@include media-breakpoint-up(xl) {
			top: calc(50% - 45px);
		}

		.icon {
			width: 50px;
			height: 50px;
			color: $bg-color;

			@include media-breakpoint-up(sm) {
				width: 70px;
				height: 70px;
			}

			@include media-breakpoint-up(xl) {
				height: 90px;
			}
		}
	}

	&__item {
		position: relative;
		z-index: 2;
		padding: 0 8px;
		cursor: pointer;
		font: {
			size: 14px;
			weight: 500;
		}

		@include media-breakpoint-up(sm) {
			padding: 0 15px;
			font: {
				size: 18px;
			}
		}

		@include media-breakpoint-up(xl) {
			padding: 0 25px;
			font: {
				size: 24px;
			}
		}
	}
}
