.catalog {
	z-index: 3;
	overflow: hidden;

	&__title {
		margin: {
			bottom: 30px;
		}

		@include media-breakpoint-up(lg) {
			margin: {
				bottom: 50px;
			}
			font: {
				size: 80px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				bottom: 80px;
			}
			font: {
				size: 120px;
			}
		}
	}

	&__list {
		@include media-breakpoint-down(sm) {
			padding: 0;
		}
	}
}
