.products {
	@extend %d-flex;
	@extend %flex-column-reverse;
	@extend %flex-md-column;

	position: relative;
	top: 0;
	bottom: 0;
	margin: {
		top: 40px;
	}

	@include media-breakpoint-up(sm) {
		width: calc(50vw - 8px);
		margin: {
			top: 0;
		}
	}

	@include media-breakpoint-up(md) {
		width: calc(50vw - 24px);
	}

	@include media-breakpoint-up(lg) {
		left: -20px;
		width: calc(50vw - 4px);
	}

	@media ( min-width: 1920px ) {
		width: calc(960px + 5px);
	}

	&__btns {
		@extend %d-flex;
		@extend %justify-content-start;
		@extend %justify-content-sm-center;
	}

	&__btn {
		@include transition(color);

		margin: 0 16px;
		color: hsl(269, 31%, 82%);
		cursor: pointer;

		&:hover {
			&:not(.is-disabled) {
				color: $text-color;
			}
		}

		.icon {
			width: 36px;
			height: 36px;

			@include media-breakpoint-up(lg) {
				width: 50px;
				height: 50px;
			}
		}

		&.is-disabled {
			opacity: .3;
			pointer-events: none;
		}
	}
}
