.text {
	width: 100%;
	line-height: 1.5;

	@include media-breakpoint-up(md) {
		max-width: 550px;
	}

	b {
		font: {
			weight: 500;
		}
	}

	h2 {
		margin: {
			bottom: 20px;
		}
		font: {
			size: 24px;
			weight: 500;
		}

		@include media-breakpoint-up(sm) {
			margin: {
				bottom: 24px;
			}
			font: {
				size: 36px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				bottom: 35px;
			}
			font: {
				size: 50px;
			}
		}
	}

	small {
		@extend %d-inline-block;

		font: {
			size: 12px;
		}

		@include media-breakpoint-up(sm) {
			font: {
				size: 14px;
			}
		}

		@include media-breakpoint-up(xl) {
			font: {
				size: 16px;
			}
		}

		+ small {
			margin: {
				top: 10px;
			}

			@include media-breakpoint-up(xl) {
				margin: {
					top: 20px;
				}
			}
		}
	}

	p {
		font: {
			size: 14px;
		}

		+ small {
			margin: {
				top: 15px;
			}

			@include media-breakpoint-up(xl) {
				margin: {
					top: 30px;
				}
			}
		}

		@include media-breakpoint-up(sm) {
			font: {
				size: 18px;
			}
		}

		@include media-breakpoint-up(xl) {
			font: {
				size: 26px;
			}
		}
	}

	.btn {
		margin: {
			top: 30px;
		}

		@include media-breakpoint-up(xl) {
			margin: {
				top: 45px;
			}
		}
	}

	&--auto {
		max-width: none;

		h2 {
			margin: {
				bottom: 0;
			}
		}
	}
}
