.footer {
	position: relative;
	z-index: 30;
	background: hsl(267, 27%, 56%);
	color: hsl(20, 16%, 96%);
	line-height: 1.5;
	padding: {
		top: 30px;
		bottom: 40px;
	}

	a {
		&:hover {
			text-decoration: underline;
		}
	}

	@include media-breakpoint-down(md) {
		font: {
			size: 14px;
		}
	}

	@include media-breakpoint-down(xs) {
		font: {
			size: 12px;
		}
	}

	&__title {
		margin: {
			bottom: 30px;
		}
		font: {
			weight: 500;
			size: 26px;
		}

		@include media-breakpoint-down(md) {
			font: {
				size: 18px;
			}
		}

		@include media-breakpoint-down(xs) {
			font: {
				size: 14px;
			}
		}
	}

	&__address {
		@extend %col-12;
		@extend %col-sm-6;
		@extend %col-md-8;
		@extend %col-xl-9;
	}

	&__address-block {
		@extend %col-12;
		@extend %col-md-6;
	}

	&__social {
		@extend %col-12;
		@extend %col-sm-6;
		@extend %col-md-4;
		@extend %col-xl-3;

		@include media-breakpoint-down(xs) {
			margin: {
				top: 30px;
			}
		}
	}
}
