.production {
	@include media-breakpoint-only(xs) {
		padding: {
			top: 10%;
		}
	}

	@include media-breakpoint-up(sm) {
		height: 300vh;
	}

	@include media-breakpoint-up(xl) {
		padding: {
			bottom: 0;
		}
	}

	&__img {
		@extend %d-none;
		@extend %d-sm-block;

		position: relative;

		.scroll {
			position: absolute;
			right: 50px;
			bottom: 100px;
			left: auto;

			@include media-breakpoint-up(xl) {
				right: 200px;
			}
		}
	}

	.headline {
		position: relative;
		z-index: 10;
	}
}
