$fp: '../fonts/';
$regular: (
	900: 'Black',
	700: 'Bold',
	500: 'Medium',
	400: 'Regular',
	300: 'Light'
);
$italic: (
	900: 'BlackItalic',
	700: 'BoldItalic',
	500: 'MediumItalic',
	400: 'Italic',
	300: 'LightItalic'
);

$caveat: (
	700: 'Bold',
	400: 'Regular',
);

@mixin font-build($family: 'GothamPro',$arr: $regular, $style: normal) {
	@each $weight, $name in $arr {
		$p: '#{$fp}#{$family}-#{$name}';

		@font-face {
			font: {
				display: swap;
				family: $family;
				weight: $weight;
				style: $style;
			}

			src: url('#{$p}.woff2') format('woff2'), url('#{$p}.woff') format('woff');
		}
	}
}

@include font-build;
@include font-build('GothamPro', $italic, italic);
@include font-build('Caveat', $caveat);
