.product {
	@extend %d-flex;
	@extend %flex-column;
	@extend %align-items-start;

	width: 172px;
	max-width: 172px;
	padding: 10px 0;
	cursor: pointer;

	@include media-breakpoint-up(sm) {
		width: 200px;
		max-width: 200px;
		padding: 10px 5px;
	}

	@include media-breakpoint-up(md) {
		width: 250px;
		max-width: 250px;
	}

	@include media-breakpoint-up(lg) {
		padding: 20px;
	}

	@include media-breakpoint-up(xl) {
		max-width: 386px;
	}

	&__photo {
		position: relative;
		height: auto;

		.icon {
			@include transition(opacity);

			position: absolute;
			z-index: 1;
			top: -20px;
			right: -20px;
			bottom: -20px;
			left: -20px;
			width: calc(100% + 40px);
			height: calc(100% + 40px);
			opacity: 0;
			pointer-events: none;

			@include media-breakpoint-up(xl) {
				top: -45px;
				right: -45px;
				bottom: -45px;
				left: -45px;
				width: calc(100% + 90px);
				height: calc(100% + 90px);
			}
		}

		img {
			position: relative;
			z-index: 2;
		}
	}

	&__info {
		padding: {
			left: 20px;
		}
	}

	&__name {
		margin: {
			top: 8px;
		}
		font: {
			family: 'Caveat';
			weight: 700;
			size: 22px;
		}

		@include media-breakpoint-up(sm) {
			margin: {
				top: 14px;
			}
			font: {
				size: 24px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				top: 22px;
			}
			font: {
				size: 40px;
			}
		}

		line-height: 1;
	}

	&__mass {
		margin: {
			top: 8px;
		}
		font: {
			size: 12px;
		}

		@include media-breakpoint-up(sm) {
			margin: {
				top: 14px;
			}
			font: {
				size: 18px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				top: 32px;
			}
			font: {
				size: 24px;
			}
		}
	}

	&:hover {
		.icon {
			opacity: 1;
		}
	}
}
