.section {
	position: relative;
	z-index: 1;

	&__reverse-mobile {
		@include media-breakpoint-down(xs) {
			flex-direction: column-reverse;
		}
	}

	&__header {
		@extend %col-12;

		&--center {
			text-align: center;
		}

		.text {
			margin: 0 auto;
		}
	}

	&--white {
		padding: {
			top: 6%;
			bottom: 6%;
		}
	}

	&--yellow {
		padding: {
			top: 3%;
			bottom: 3%;
		}
		background: {
			color: hsl(54, 23%, 91%);
			image: url(/images/standard/bg.png);
		}
	}

	&--violet {
		padding: {
			top: 3%;
			bottom: 3%;
		}
		background: {
			color: hsl(270, 31%, 84%);
			image: url(/images/standard/bg.png);
		}
	}

	&__block {
		@extend %col-12;
		@extend %col-sm-6;
	}
}

.down {
	overflow: hidden;
}
