.actions {
	&__link {
		@include transition(box-shadow);

		@extend %d-flex;

		overflow: hidden;
		border-radius: 10px;

		@include media-breakpoint-up(md) {
			border-radius: 40px;
		}

		&:hover {
			box-shadow: 0 -2px 10px hsla(240, 4%, 5%, .7);
		}

		img {
			width: 100%;
		}
	}

	.btn {
		margin: {
			top: 40px;
		}

		@include media-breakpoint-up(md) {
			margin: {
				top: 80px;
			}
		}
	}

	&__list {
		.actions__item {
			@extend %col-12;
			@extend %col-sm-6;
			@extend %col-md-4;
			margin: {
				bottom: 15px;
			}

			@include media-breakpoint-up(md) {
				margin: {
					bottom: 30px;
				}
			}
		}
	}

	&__footer {
		text-align: center;
		padding: {
			bottom: 4%;
		}
	}
}
