.welcome {
	z-index: 2;
	padding: {
		bottom: 0;
	}

	&__photo {
		@extend %align-self-end;
	}

	&__img {
		@extend %d-block;

		margin: 0 auto;

		@include media-breakpoint-down(md) {
			width: 100%;
			max-width: 500px;
		}
	}

	&__title {
		position: relative;
		margin: {
			bottom: 30px;
		}
		font: {
			size: 42px;
		}

		@include media-breakpoint-up(sm) {
			margin: {
				bottom: 50px;
			}
			font: {
				size: 60px;
			}
		}

		@include media-breakpoint-up(md) {
			font: {
				size: 74px;
			}
		}

		@include media-breakpoint-up(lg) {
			left: -60px;
			font: {
				size: 90px;
			}
		}

		@include media-breakpoint-up(xl) {
			left: -100px;
			margin: {
				bottom: 80px;
			}
			font: {
				size: 132px;
			}
		}
	}

	&__scroll {
		margin: {
			top: 140px;
			bottom: 100px;
		}

		@include media-breakpoint-up(xl) {
			margin: {
				margin: 160px;
			}
		}
	}
}
