
.list-enter {
	transform: translateX(300px);
	opacity: 0;
}

.list-enter-active,
.list-leave-active,
.list-move {
	@include transition('all', 500ms);
}

.list-enter-to {
	transform: translateX(0);
	opacity: 1;
}

.list-leave-active {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
}

.list-leave-to {
	opacity: 0;
}

