.article {
	padding: {
		top: 8%;
		bottom: 0;
	}

	&__date {
		color: hsl(267, 27%, 56%);
		font-size: 12px;
		font-weight: 500;
		margin: {
			bottom: 20px;
		}

		@include media-breakpoint-up(md) {
			font-size: 16px;
			margin: {
				top: 30px;
			}
		}

		@include media-breakpoint-up(xl) {
			font-size: 18px;
			margin: {
				top: 50px;
			}
		}
	}

	&__title {
		line-height: 1.5;

		font: {
			weight: 400;
			size: 14px;
		}

		+ .text {
			margin: {
				top: 15px;
			}

			@include media-breakpoint-up(xl) {
				margin: {
					top: 30px;
				}
			}
		}

		@include media-breakpoint-up(sm) {
			font: {
				size: 18px;
			}
		}

		@include media-breakpoint-up(xl) {
			font: {
				size: 26px;
			}
		}
	}

	&__return {
		@extend %d-inline-block;

		font-size: 14px;
		font-weight: 500;
		margin: {
			top: 30px;
		}

		@include media-breakpoint-up(md) {
			font-size: 18px;
			margin: {
				top: 50px;
			}
		}

		@include media-breakpoint-up(xl) {
			font-size: 20px;
			margin: {
				top: 85px;
			}
		}

		&:hover {
			text-decoration: underline;
		}
	}

	&__banner {
		overflow: hidden;
		border-radius: 40px;
		margin: {
			top: 30px;
		}

		img {
			width: 100%;
		}

		@include media-breakpoint-only(xs) {
			border-radius: 10px;
			margin: {
				top: none;
				bottom: 30px;
			}
		}
	}
}
