.shops {
	@extend %d-flex;
	@extend %flex-column;
	@extend %align-items-center;

	position: relative;
	z-index: 3;
	width: 100%;
	height: 100%;
	padding: 60px 20px;

	@include media-breakpoint-up(lg) {
		padding: 60px;
	}

	@include media-breakpoint-up(xl) {
		padding: 80px 100px;
	}

	.headline {
		margin: {
			bottom: 26px;
		}
		font: {
			weight: 400;
			size: 26px;
		}

		@include media-breakpoint-up(lg) {
			margin: {
				bottom: 40px;
			}
			font: {
				size: 60px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				bottom: 60px;
			}
			font: {
				size: 90px;
			}
		}

		.icon {
			color: hsl(269, 31%, 82%);
		}
	}

	&__icon {
		position: absolute;
		bottom: -2px;
		left: 18px;
		width: 44px;
		height: 44px;
		color: hsl(20, 16%, 96%);

		@include media-breakpoint-up(lg) {
			bottom: 45px;
			left: 42px;
			width: 70px;
			height: 70px;
		}

		@include media-breakpoint-up(xl) {
			bottom: 64px;
			left: 35px;
			width: 90px;
			height: 90px;
		}

		&--small {
			bottom: -11px;
			left: 72px;
			width: 16px;
			height: 16px;

			@include media-breakpoint-up(lg) {
				bottom: 33px;
				left: 129px;
				width: 22px;
				height: 22px;
			}

			@include media-breakpoint-up(xl) {
				bottom: 52px;
				left: 142px;
				width: 30px;
				height: 30px;
			}
		}
	}

	&__list {
		@extend %d-flex;
		@extend %flex-wrap;
		@extend %justify-content-center;
	}

	&__item {
		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-center;

		width: 80px;
		height: 40px;
		margin: 10px;

		@include media-breakpoint-up(lg) {
			width: 120px;
			height: 50px;
			margin: 30px;
		}

		@include media-breakpoint-up(xl) {
			width: 170px;
			height: 70px;
			margin: 30px;
		}

		img {
			width: auto;
			max-width: 80px;
			height: auto;
			max-height: 40px;

			@include media-breakpoint-up(lg) {
				max-width: 120px;
				max-height: 50px;
			}

			@include media-breakpoint-up(xl) {
				max-width: 170px;
				max-height: 70px;
			}
		}
	}
}
