@import 'node_modules/css-reset-and-normalize/scss/reset-and-normalize.scss';
@import 'node_modules/css-reset-and-normalize/scss/button-reset.scss';
@import 'node_modules/css-reset-and-normalize/scss/link-reset.scss';

* {
	box-sizing: border-box;
}

html,
body {
	@extend %d-flex;
	@extend %flex-column;

	min-height: 100vh;
	color: $text-color;
	-webkit-font-smoothing: antialiased;
	line-height: 1.2;
	user-select: none;
	background: {
		color: $bg-color;
	}
	font: {
		family: 'GothamPro', sans-serif;
		size: 18px;
		weight: 400;
	}
}

img {
	@extend %d-block;

	width: auto;
	max-width: 100%;
	height: auto;
	max-height: 100%;
	pointer-events: none;
}

picture {
	@extend %d-block;

	width: 100%;
}

input,
textarea {
	&:focus {
		outline: none;
	}
}

body {
	overflow-x: hidden;

	&.blocked-scroll {
		overflow: hidden;
	}
}
