.step {
	@extend %d-flex;
	@extend %flex-column;

	position: relative;
	margin: {
		top: 80px;
	}

	@include media-breakpoint-up(md) {
		margin: {
			top: 90px;
		}
	}

	@include media-breakpoint-up(xl) {
		margin: {
			top: 140px;
		}
	}

	@media ( min-height: 800px ) {
		margin: {
			top: 140px;
		}
	}

	&--mobile {
		@extend %d-sm-none;
	}

	&__info {
		@extend %d-inline-block;

		position: relative;
		z-index: 4;
		max-width: 260px;
		// background-color: rgb(96, 181, 255);

		@include media-breakpoint-up(md) {
			max-width: 520px;
			height: 350px;
		}

		@include media-breakpoint-up(xl) {
			max-width: 580px;
			height: 510px;
		}

		~ .step__info {
			@include media-breakpoint-only(xs) {
				margin: {
					top: -100px;
				}
			}
		}
	}

	&__content {
		z-index: 2;
	}

	&__content-img {
		position: absolute;
		color: hsl(288, 14%, 93%);

		@include media-breakpoint-only(xs) {
			right: 74px;
			bottom: -139px;
			width: 50px;
			height: 50px;
		}

		@include media-breakpoint-up(sm) {
			top: calc(50% - 50px);
			left: -243px;
			width: 100px;
			height: 100px;
		}

		@include media-breakpoint-up(md) {
			left: -196px;
		}

		@include media-breakpoint-up(xl) {
			top: calc(50% - 60px);
			left: -266px;
			width: 120px;
			height: 120px;
		}
	}

	&__icon {
		position: absolute;
		z-index: 1;
		top: -80px;
		right: -80px;
		bottom: -80px;
		left: -80px;
		color: hsla(269, 31%, 82%, .2);
		pointer-events: none;

		@include media-breakpoint-up(sm) {
			top: -140px;
			right: -140px;
			bottom: -140px;
			left: -140px;
		}

		.icon {
			width: 100%;
			height: 100%;

			&--desktop {
				@extend %d-none;
				@extend %d-sm-block;
			}

			&--mobile {
				@extend %d-sm-none;

				color: hsl(288, 14%, 93%);
			}
		}

		&::before {
			content: attr(data-title);
			position: absolute;
			z-index: 10;
			top: 22px;
			left: 92px;
			transform: rotate(-7.13deg);
			color: hsl(266, 44%, 74%);
			font: {
				family: 'Caveat';
				size: 26px;
			}

			@include media-breakpoint-up(sm) {
				top: 89px;
				left: 74px;
				font: {
					size: 36px;
				}
			}

			@include media-breakpoint-up(xl) {
				top: 44px;
				left: 86px;
				font: {
					size: 50px;
				}
			}
		}

		&--second,
		&--third {
			@include media-breakpoint-up(xl) {
				top: -180px;

				&::before {
					top: 78px;
				}
			}
		}
	}

	.text {
		position: relative;
		z-index: 3;
	}

	&__picture {
		position: relative;
		z-index: 4;
		width: 260px;
	}

	&__block {
		@extend %d-block;

		position: relative;
		z-index: 3;
		margin: {
			top: 16px;
		}
		font: {
			family: 'Caveat';
		}

		@include media-breakpoint-up(xl) {
			margin: {
				top: 32px;
			}
		}
	}

	&__title {
		margin: {
			bottom: 2px;
		}
		font: {
			size: 20px;
		}

		line-height: 1.2;

		@include media-breakpoint-up(sm) {
			margin: {
				bottom: 12px;
			}
		}

		@include media-breakpoint-up(md) {
			font: {
				size: 24px;
			}
		}

		@include media-breakpoint-up(xl) {
			font: {
				size: 36px;
			}
			margin: {
				bottom: 24px;
			}
		}
	}

	&__header {
		color: hsl(267, 42%, 71%);
		font: {
			family: 'GothamPro';
			weight: 500;
			size: 40px;
		}

		@include media-breakpoint-only(sm) {
			font: {
				size: 24px;
			}
		}

		@include media-breakpoint-up(xl) {
			font: {
				size: 60px;
			}
		}
	}

	&__list {
		@extend %d-flex;
		@extend %flex-wrap;

		width: 100%;

		@include media-breakpoint-only(xs) {
			justify-content: center;
		}
	}

	&__item {
		@extend %d-flex;
		@extend %flex-column;
		@extend %align-items-center;

		position: relative;
		width: 120px;
		font: {
			size: 16px;
		}

		@include media-breakpoint-only(xs) {
			min-width: 36%;
			margin: {
				bottom: 10px;
			}
		}

		@include media-breakpoint-up(xl) {
			font: {
				size: 24px;
			}
		}

		&:not(:last-child) {
			margin: {
				right: 10px;
			}

			@include media-breakpoint-only(xs) {
				margin: {
					right: 0;
				}
			}

			@include media-breakpoint-up(xl) {
				margin: {
					right: 20px;
				}
			}
		}

		&--text {
			@extend %align-items-start;

			width: auto;

			@include media-breakpoint-only(xs) {
				width: 100%;
			}

			&:not(:last-child) {
				margin: {
					right: 40px;
				}
			}
		}

		.icon {
			width: 44px;
			height: 44px;
			color: hsl(266, 44%, 74%);

			@include media-breakpoint-up(xl) {
				width: 66px;
				height: 66px;
			}

			&--xl {
				width: 60px;
				height: 60px;

				@include media-breakpoint-up(xl) {
					width: 96px;
					height: 96px;
				}
			}
		}
	}

	&__item-logo {
		position: absolute;
		top: 18px;
		left: calc(50% - 12px);
		width: 25px;

		@include media-breakpoint-up(xl) {
			top: 34px;
			left: calc(50% - 17px);
			width: 35px;
		}

		&--v2 {
			top: 15px;

			@include media-breakpoint-up(xl) {
				top: 27px;
			}
		}
	}

	&__label {
		@extend %d-block;

		line-height: 1.2;
		text-align: center;
		margin: {
			top: 0;
		}

		@include media-breakpoint-up(sm) {
			margin: {
				top: 6px;
			}
		}
	}
}
