.question {
	z-index: 10;
	padding: {
		top: 0;
		bottom: 0;
	}

	.section {
		&__block {
			@extend %col-12;
			@extend %col-sm-12;
			@extend %col-md-6;
		}

		&__reverse-mobile {
			@include media-breakpoint-down(sm) {
				flex-direction: column-reverse;
			}
		}
	}

	&__form {
		@include media-breakpoint-up(md) {
			padding: {
				bottom: 50px;
			}
		}

		@include media-breakpoint-up(xl) {
			padding: {
				bottom: 85px;
			}
		}
	}
}
