.about {
	z-index: 10;
	padding: {
		top: 8%;
		bottom: 12%;
	}

	&__header {
		.text {
			margin: {
				top: 30px;
			}

			@include media-breakpoint-up(md) {
				margin: {
					top: 50px;
				}
			}

			@include media-breakpoint-up(xl) {
				margin: {
					top: 70px;
				}
			}
		}
	}

	&__list {
		margin: {
			top: 36px;
		}

		@include media-breakpoint-up(md) {
			margin: {
				top: 60px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				top: 110px;
			}
		}
	}

	&__item {
		@extend %col-6;
		@extend %col-sm-3;
	}

	&__footer {
		margin: {
			top: 30px;
		}

		text-align: center;

		@include media-breakpoint-up(md) {
			margin: {
				top: 40px;
			}
		}

		@include media-breakpoint-up(xl) {
			margin: {
				top: 70px;
			}
		}
	}

	&__text {
		max-width: 389px;
		margin: 0 auto 30px;
		color: hsl(267, 43%, 71%);
		font: {
			family: 'Caveat';
			size: 20px;
		}

		line-height: 1.2;

		@include media-breakpoint-up(md) {
			margin: 0 auto 40px;
			font: {
				family: 'Caveat';
				size: 24px;
			}
		}
	}
}
